import { AccountEntity } from "@models/account/entity";

export class AddAccountToUserResponse {
  private constructor(
    private readonly success: boolean,
    private readonly data?: AccountEntity,
    private readonly error?: string,
    private readonly validationErrors?: string[]
  ) {}

  public static success(data: AccountEntity): AddAccountToUserResponse {
    return new AddAccountToUserResponse(true, data);
  }

  public static failure(error: string): AddAccountToUserResponse {
    return new AddAccountToUserResponse(false, undefined, error);
  }

  public static validationFailure(errors: string[]): AddAccountToUserResponse {
    return new AddAccountToUserResponse(false, undefined, undefined, errors);
  }

  public isSuccess(): boolean {
    return this.success;
  }

  public getData(): AccountEntity | undefined {
    return this.data;
  }

  public getError(): string | undefined {
    return this.error;
  }

  public getValidationErrors(): string[] | undefined {
    return this.validationErrors;
  }
}
